@import url(https://fonts.googleapis.com/css2?family=Vollkorn+SC&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Arizonia&display=swap);
body {
  font-family:"Vollkorn SC";
  margin: 0;
  display:flex;
  justify-content:center;
  align-items:center;
  width:100%;
  height:100vh;
  background-color:#DADED4;
  -ms-scroll-chaining:none;
      overscroll-behavior:none;
}


#buttons{
    margin:0 .7em;
}
#timer{
    width:200px;
    font-size:2.5em;
    display:flex;
    flex-direction:column;
    align-items:center;
    border:3px solid white;
    padding:.5em 1em .2em 1em;
    margin:.5em 0;
    border-radius:5px;
}
#minute-second{
    display:flex;
    font-size:2em;
}
.blink{
    -webkit-animation:blink 1s ease-in-out infinite;
            animation:blink 1s ease-in-out infinite;
}
@-webkit-keyframes blink{
    from{
        opacity:1;
    }to{
        opacity:0;
    }
}
@keyframes blink{
    from{
        opacity:1;
    }to{
        opacity:0;
    }
}


#inputs-wrapper{
    display:flex;
    justify-content:space-evenly;
    font-size:2em;
}
h3{
    font-size:1.7em;
    margin:0 .3em;
}
#length{
    margin:1em 1em;
}
#page-wrapper{
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  margin-top:3em;
}
#pomodoro-clock{
  width:100%;
  color:#3C403D;
  display:flex;
  flex-direction:column;
  align-items:center;
  border:10px solid white;
  position:relative;
  box-shadow:1px 1px 10px 1px #3C403D;
  padding:0 0 2em 0;
}
#length-wrapper{
  display:flex;
}
.length-setter{
  display:flex;
}
#function-button{
  display:flex;
  font-size:1.7em;
}
.icon{
  cursor:pointer;
  transition-duration:.3s;
}
.icon:hover{
  color:white;
  transition-duration:.3s;
  transform:scale(1.1);
}
#footer{
  display:flex;
  flex-direction:column;
  align-items:center;
  justify-content:center;
  line-height:.1em;
  margin-top:4em;
  font-size:.9em;
  color: #3C403D;
  
}
#name ,#name a{
  font-family:"Arizonia";
  font-size:1.3em;
  text-decoration:none;
  color: #3C403D;
}
